import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-marketitem',
  templateUrl: 'marketitem.html',
})
export class MarketitemPage {
  
  public i : any ;
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {

    this.i = {
     img : './assets/img/blank.png',
     title : '',
     description : '',
     price : '',
     contact : '',
     email : '',
     phone : ''
    }
    
        
  }

  ionViewDidLoad() {
          let that = this;
      let url = 'zs_marketitem.php?id='+this.navParams.get('id');
console.log(url);
       this.connect.getList(url).subscribe(
          data => {
console.log(data);
             that.i = data;
          },
          err => {
          }
        );
  }

}



