import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { MarketsellPage } from '../marketsell/marketsell';
import { MarketitemPage } from '../marketitem/marketitem';
import { LoginPage } from '../login/login';
import { SponsorPage } from '../sponsor/sponsor';

@Component({
  selector: 'page-market',
  templateUrl: 'market.html',
})
export class MarketPage {
 public banner : any = './assets/img/blankbanner.jpg';
 public items : any = [];
 public srch : any = ''; 
 public grp : any = 1;
 public catname : any = 'NEW';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
     this.items = [];
     this.srch='';
  }

  ionViewDidEnter() {
    let i = this.navParams.get("id");
    this.grp=i;
    this.loadtab(i);
  }

  doSrch() {
    this.loadtab(this.grp);
  }

  goItem(a)
  { this.navCtrl.push( MarketitemPage, { id : a});
  }

  loadtab(i) {
          
       let that = this;
       this.grp = i;
       switch (this.grp) {
          case 1 : this.catname='NEW'; break;
          case 2 : this.catname='USED'; break;
          case 3 : this.catname='SWAP'; break;
          case 4 : this.catname='FREE';
       }

       let url = 'zs_getmarket.php?id='+i+'&srch='+encodeURI(this.srch);
       this.connect.getList(url).subscribe(
          data => {
            that.items = data.items;
            that.banner = data.banner;
          },
          err => {
          }
        );
  }

  doList()
  {
   let me = window.localStorage.getItem('myac');
   if (!me) me='';
   if (me.length) 
     this.navCtrl.push( MarketsellPage, { grp : this.grp});
   else 
     this.navCtrl.push( LoginPage);

  }

  showSponsor()
  { this.navCtrl.push( SponsorPage, { id : 1});
  }

}



