import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { EpayPage } from '../epay/epay';
import { LoginPage } from '../login/login';

@Component({
  selector: 'page-events',
  templateUrl: 'events.html',
})
export class EventsPage {

  public events : any = [];
  public mode : any = 0;
  public saveid : any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    this.events = [];
  }

  ionViewDidEnter() {
        let that=this;
        let url = 'zs_event.php';
        this.connect.getList(url).subscribe(
         data => {
            that.events = data.events;
         },
         err => {
         }
        ); 
  }

 mqty(a,b) {
  this.events[a].qty = Math.max(0,this.events[a].qty + b);
 }

 doSubmit(a) {
   this.saveid = a;
   let me = window.localStorage.getItem('myac');
   if (!me) me='';
   if (me.length) 
     this.navCtrl.push( EpayPage, { id : this.events[a].id, qty : this.events[a].qty });
   else 
     this.navCtrl.push( LoginPage);
 }

}



