import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-feedback',
  templateUrl: 'feedback.html',
})
export class FeedbackPage {


  concern : any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController, public loadingCtrl: LoadingController, private connect : Connect) {

     
     this.concern = {
      name : '',
      contact : '',
      concern : ''
     }
  }

  ionViewDidLoad() {
     
  }


  showOk() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Your feedback has been sent',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }
  

 dosubmit() {

      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
  
   let url = 'zs_feedback.php?reg='+JSON.stringify(this.concern);
console.log(url);
       this.connect.getList(url).subscribe(
          data => {
            loading.dismiss();
            that.showOk();
          },
          err => {
            loading.dismiss();
            
          }
        );

 }

}




