import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { HTTP } from '@ionic-native/http';
import { Connect } from '../../providers/connect';
@Component({
  selector: 'page-marketsell',
  templateUrl: 'marketsell.html',
})
export class MarketsellPage {

  pictaken : any = false;
  su : any;
  img : any = './assets/img/takepic2.jpg';
  me : any = 0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private camera: Camera, public http : HTTP, public loadingCtrl: LoadingController, private connect : Connect) {

     this.me = window.localStorage.getItem('myac');
     
     this.su = {
      title : '',
      me : window.localStorage.getItem('myac'),
      contact: '',
      email : '',
      phone : '',
      content: '',
      grp : 1, 
      picdata: ''
     }
     this.su.grp = this.navParams.get("grp");
  }

  ionViewDidLoad() {
     this.me = window.localStorage.getItem('myac');
     let that = this;
      let url = 'zs_marketcontact.php?id='+this.me;
       this.connect.getList(url).subscribe(
       data => {
           that.su.contact = data.contact;
           that.su.email = data.email;
       },
       err => {
         }
       ); 
     
  }

  getpicture() {
 
   const options: CameraOptions = {
     quality: 70,
     targetWidth:500,
     targetHeight:500,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pictaken = true;
    that.su.picdata = imageData;
    that.img = 'data:image/jpeg;base64,' + imageData;
 }, (err) => {
    alert(JSON.stringify(err));
 });

 }

  getcameraroll() {
   const options: CameraOptions = {
     quality: 70,
     targetWidth:500,
     targetHeight:500,
     sourceType: 0,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pictaken = true;
    that.su.picdata = imageData;
    that.img = 'data:image/jpeg;base64,' + imageData;

 }, (err) => {
    alert(JSON.stringify(err));
 });

 }

 dosubmit() {

      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://pikcup.com.au/test/zs_marketupload.php';
    this.http.post(url, that.su, {})
      .then(data => {
            loading.dismiss();
 	  that.navCtrl.pop();
    })
    .catch(error => {
            loading.dismiss();
     
  });

 }

}




