import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';

import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-epay',
  templateUrl: 'epay.html',
})
export class EpayPage {

  public payment: any = '';
  public paycard : any = '';
  public pmethods : any[];
  public allok : any = false; 
  public pmode : any = 1;
  public id : any = 0;

  public stripe_pub: any = '';
  public name : any = '';
  public qty : any = 1;
  public amount : any = 0;
  public total : any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public connect: Connect, public alertCtrl : AlertController) {
       this.pmethods = [];
       this.allok = false;
       this.pmode = 1;
       this.id = this.navParams.get('id');
       this.qty = this.navParams.get('qty');
  }

  ionViewDidEnter() {
    this.recalc();
  }

    recalc() {
     this.payment = window.localStorage.getItem('userPayment');
     if (!this.payment)
       this.payment='';
     if (this.payment.length) 
        this.allok = true;

         let that=this;
	    let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();
         let url='zs_getepaymentdetails.php?pmode='+this.pmode+'&me='+window.localStorage.getItem('myac')+'&qty='+this.qty+'&id='+this.id+'&payment='+this.payment;
          this.connect.getList(url).subscribe(
         data => {
             loader.dismiss();
             that.paycard = data.paycard;
             that.pmethods = data.pmethods;
             that.stripe_pub = data.stripe_pub;
             that.payment = data.payment;
             that.name = data.name;
             that.amount = data.amount;
             that.total = data.total;
             window.localStorage.setItem('userPayment',that.payment);
	     if (that.payment.length) 
        	that.allok = true;
             else
                that.allok=false;
         },
         err => { loader.dismiss();
                this.connect.logError(err);
         }
        ); 

  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Payment is complete',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             window.localStorage.setItem('goback','yes');
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  doError() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Warning',
      subTitle: 'Payment could not be completed - Please try again',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }


  showPayment(a)
  { window.localStorage.setItem('userPayment',a);
    this.pmode=0;
    this.recalc();
  }
  
  showEnterPayment() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }

  doConfirm() {
            let that = this;
	    let loader = this.loadingCtrl.create({
	      content: "Please wait..."
	    });
	    loader.present();
            let url='zs_saveepayment.php?reg='+this.id+'^'+this.payment+'^'+this.qty+'^'+window.localStorage.getItem('myac');
      console.log(url);
         //  this.myAlert(url);
             this.connect.getList(url).subscribe(
	         data => {
		    loader.dismiss();
                    if (data.success) 
                      that.doComplete();
                    
                    else
                      that.doError();
                 },
	         err => {
                    loader.dismiss();
                    this.connect.logError(err);
                 }
	        );


  }

}




