import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ProfilePage } from '../profile/profile';
import { SponsorPage } from '../sponsor/sponsor';

@Component({
  selector: 'page-teamplayers',
  templateUrl: 'teamplayers.html',
})
export class TeamplayersPage {
  players : any = [];
  feed : any = [];
  teamname : any;
  team : any = 0;
  mode : any  = 3;
  msg : any = '';

  banner : any = 'https://theparcelpeople.com.au/soccer/banner.jpg';
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    let tt = this.navParams.get('t');
    this.teamname = tt.name;
    this.team = tt.id;
    this.msg = '';
  }

  ionViewDidLoad() {
     this.mode = 3;
     let that=this;
     let me = window.localStorage.getItem('myac');
     if (!me) me='0';
        let url = 'zs_teaminfo.php?id='+this.team+'&me='+me;
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
            that.feed = data.feed;
            that.msg = data.msg;
         },
         err => {
         }
        ); 

  }
 
  gomode(a) {
    this.mode = a;
  }

  goPlayer(p) {
    this.navCtrl.push(ProfilePage,{player : p});
  }

  showSponsor() {
    this.navCtrl.push(SponsorPage, { id : 1});
  }

uptick(a)
 { if (!this.feed[a].liked) {

     this.feed[a].likes++;
     this.feed[a].liked=true;
     let that=this;
        let url = 'zs_uptick.php?id='+this.feed[a].id;
        this.connect.getList(url).subscribe(
         data => {
         },
         err => {
         }
        ); 
   }
 }

}



