import { Component } from '@angular/core';
import {  NavController, LoadingController, ToastController, NavParams, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';

@Component({
  selector: 'page-indoor',
  templateUrl: 'indoor.html',
})
export class IndoorPage {

  public reg : any;
  public teams : any = [];
  public payment: any = '';
  public paycard : any = '';
  public pmethods : any[];
  public allok : any = false; 
  public pmode : any = 1;
  public id : any = 0;

  public stripe_pub: any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect, public loadingCtrl : LoadingController, public toastCtrl : ToastController, public alertCtrl : AlertController) {
    this.reg = {
      id : '',
      teamname: '',
      yourname: '',
      phoneh:'',
      email: '',
      division : 0, 
    }
    this.teams=[];
    this.pmethods = [];
       this.allok = false;
       this.pmode = 1;

  }

  ionViewDidLoad() {
       let that = this;
       let me = window.localStorage.getItem('myac');
       let url = 'zs_indoor.php?me='+me;
       this.connect.getList(url).subscribe(
          data => {
             that.teams = data.teams;
             that.reg = data.reg;
          },
          err => {
            that.connect.logError(err)
          }
        );
   
  }

 ionViewDidEnter() {
   if (window.localStorage.getItem('goback')=='yes') {
     window.localStorage.removeItem('goback');
     this.navCtrl.pop();
   }
   else this.recalc();
 }

    recalc() {
     this.payment = window.localStorage.getItem('userPayment');
     if (!this.payment)
       this.payment='';
     if (this.payment.length) 
        this.allok = true;

         let that=this;
	  let url='zs_getepaymentdetails.php?pmode='+this.pmode+'&me='+window.localStorage.getItem('myac')+'&qty=1&id=1&payment='+this.payment;
          this.connect.getList(url).subscribe(
         data => {
             that.paycard = data.paycard;
             that.pmethods = data.pmethods;
             that.stripe_pub = data.stripe_pub;
             that.payment = data.payment;
        
             window.localStorage.setItem('userPayment',that.payment);
	     if (that.payment.length) 
        	that.allok = true;
             else
                that.allok=false;
         },
         err => {
         }
        ); 

  }

 private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  showOk() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Registration is complete',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             window.localStorage.setItem('goback','yes');
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  doRegister()
  { let msg='';
    if (!this.reg.division) msg="Please choose a season";
    if (this.reg.yourname.length<1)
      msg="Please enter your name";

    if (msg.length) 
      this.presentToast(msg);
    else {
       let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'zs_indoorregister.php?me='+window.localStorage.getItem('myac')+'&reg='+JSON.stringify(this.reg);
 console.log(url);

       this.connect.getList(url).subscribe(
          data => {
            loading.dismiss();
            if (data.success) {
              that.showOk();
            }
          },
          err => {
            loading.dismiss();
            that.presentToast("Could not register team");
            that.connect.logError(err)
          }
        );

    }
  }

  showPayment(a)
  { window.localStorage.setItem('userPayment',a);
    this.pmode=0;
    this.recalc();
  }
  
  showEnterPayment() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }


}



