import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the TipspastPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tipspast',
  templateUrl: 'tipspast.html',
})
export class TipspastPage {
  public tips : any = [];
  public tick : any = './assets/img/tick.png';

  public cross : any = './assets/img/cross.png';


  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
  }

  ionViewDidLoad() {
      let that = this;
     this.tips = [];
     let url = 'zs_tipspast.php?id='+window.localStorage.getItem('tiplayer');
       this.connect.getList(url).subscribe(
       data => {
console.log(data);
           that.tips = data.tips;
       },
       err => {
         }
       ); 

  }

}



